/* body,
html {
    min-height: 100%;
} */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-align: center;
}

.app-div {
  position: relative;
  /* min-height: 100%; */
  margin: 8px auto;
}
.header {
  padding: 10px;
  margin: auto;
  background-color: #fff;
}

/* Titre "Terfs vs Pikachu */
.main-container h1 {
  margin: 0;
  color: #535353;
  font-family: "Krona One", Arial, Helvetica, sans-serif;
  font-size: 2em;
  letter-spacing: 8px;
}

/* MODAL */
.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #8181813d;
}
.modal > div {
  position: relative;
  top: -25%;
  width: 20%;
  height: 230px;
  padding: 20px;
  width: 600px;
  border-radius: 5px;
  background-color: #ffffffc9;
}
.modal h4,
h5,
h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0;
}
.modal h3 {
  font-size: 35px;
}
.modal h4 {
  font-size: 20px;
}
.modal h5 {
  font-size: 12px;
}
.modal span {
  font-weight: 700;
}

/* MAIN CONTENT */
#h1-pikachu,
#h1-terfs {
  margin: 0;
  font-size: 4em;
  letter-spacing: 8px;
}
.pikajk {
  margin-top: -50px;
  height: 90%;
}
.pikajk img {
  margin-top: -10px;
}
.jk-tweet,
.pika-tweet {
  display: flex;
  height: 150px;
  min-width: 400px;
  overflow: scroll;
  border: double black;
  padding: 3px;
  font-family: "Courier Prime", monospace;
  margin-bottom: 30px;
}

/* Animations on attacks */
#lightning-id {
  position: fixed;
  width: 40%;
  left: 100px;
  top: -30px;
}
.hide-lightning {
  display: none;
}
#vagina-id {
  z-index: 99;
  transform: rotate(15deg);
  position: fixed;
  width: 40%;
  right: 100px;
  top: 170px;
}
.hide-vagina {
  display: none;
}

.jk-tweet span,
.pika-tweet span {
  display: block;
  margin: auto;
  padding: 0 10px;
}

/* Progress bar */
.progress {
  margin: auto;
  width: 80%;
  border-radius: 50px;
  border: 1px solid black;
  padding: 3px;
}
.progress-bar {
  border-radius: 50px;
}
.disabled-bar {
  border: 1px solid #fff;
}

/* K.O Text */
.text-ko {
  display: block;
  margin: auto;
  padding: 0 10px;
  font-size: 20px;
  font-family: "Courier Prime", monospace;
}

/* //K.O Animation */
#btn-fixed {
  margin: auto;
  align-items: center;
}
.blink-class {
  transition: transform 1s, opacity 0.5s ease-in;
}
.ko-animation {
  animation-duration: 800ms;
  animation-name: slidedown;
  animation-fill-mode: forwards;
}

@keyframes slidedown {
  from {
    opacity: 1;
  }
  25% {
    transform: translateY(-40px);
    opacity: 0.8;
  }
  70% {
    opacity: 0;
  }

  to {
    transform: translateY(400px);
    opacity: 0;
  }
}

/* // FOOTER // */
footer {
  display: flex;
  margin-top: 10%;
  font-size: 11px;
  background-color: #fff;
  color: #535353;
}
.elem {
  white-space: wrap;
}
/* Icones custom */
#logos a {
  margin: 0 10px;
  color: #000;
  opacity: 0.6;
  transition: all ease 300ms;
  padding-bottom: 10px;
}
#logos a:hover {
  opacity: 0.5;
}
#logos a i {
  color: #535353;
}

/* // MEDIA QUERIES */
/* // (for xxl screens) */

@media all and (min-width: 1400px) {
  .app-div {
    max-width: 1280px;
  }
  .jk-tweet,
  .pika-tweet {
    margin-top: 5%;
  }
  .pikajk img {
    margin: 10% 0 5% 0;
  }
  .btn {
    margin: 20px 0 30px 0 !important;
  }
  /* Tweets display */
  .jk-tweet,
  .pika-tweet {
    margin: 40px 50px;
    height: 200px;
    width: 500px;
  }

  /* //GIF POSITION */
  #lightning-id {
    width: 30%;
    left: 270px;
    top: -240px;
  }
  #vagina-id {
    width: 30%;
    right: 350px;
    top: 0px;
  }
}

/* // (for XS / S screens) */
@media all and (max-width: 800px) {
  /* pikachu under jkrowling */
  /* New test : */
  .jk-tweet,
  .pika-tweet {
    min-width: 100%;
  }

  .pikajk {
    display: flex;
    flex-direction: column;
  }
  .row-pika {
    margin-top: 20px;
  }
  /* //GIF POSITION */
  #lightning-id {
    width: 40%;
    left: 70px;
    top: 200px;
  }
  #vagina-id {
    width: 40%;
    right: 100px;
    top: 300px;
  }
}
